<script>
import axios from 'axios'
import router from "../router/index";
import currency from '../currency.json'
import countries from '../data.json'

export default {
    name: 'AddItem',
    components: {

    },
    data: ()=>({
        valid: false,
        dialog: false,
        newItem: {
            sender_name: '',
            sender_surname: '',
            sender_country: '',
            sender_city: '',
            receiver_name: '',
            receiver_surname: '',
            receiver_id: '',
            receiver_country: '',
            receiver_city: '',
            receiver_address: '',
            receiver_number: '',
            weight: '',
            price: '',
            currency: '',
            in_manifest: '',
            manifest_number: null,
            description: '',
        },
        manifestList: [],
        currency: currency,
        countries: countries, 
        nameRules: [
            v => !!v || '  ',
            v => v.length <= 150 || '  ',
        ],
        email: '',
        weightRules: [
            v => /^-?\d*\.?\d*$/.test(v) || '   ',
        ],
        priceRules: [
            v => /^-?\d*\.?\d*$/.test(v) || '   ',
        ],
        receiverNumberRules: [
            v => /^\d+$/.test(v) || '    ',
        ],
    }),

    methods: {
        addItem(){
            console.log(this.newItem)
            let accessToken = JSON.parse(sessionStorage.getItem('access'))
            const baseURL = `http://127.0.0.1:8000/items/add/`;
            const options = {
                method: 'POST',
                baseURL: baseURL,
                timeout: 5000,
                data: this.newItem,
                headers: {
                    Authorization: 'Bearer ' + accessToken.value
                }, 
            };
            axios(options)
            .then((response) => {
                console.log(response)
                this.$toast.success(' ', {
                    position: "bottom-left",
                    timeout: 5000,
                    closeOnClick: true,
                    pauseOnFocusLoss: true,
                    pauseOnHover: true,
                    draggable: true,
                    draggablePercent: 0.6,
                    showCloseButtonOnHover: false,
                    hideProgressBar: false,
                    closeButton: "button",
                    icon: true,
                    rtl: false
                });
                this.dialog = false
            })
            .catch((error) => {
                console.log(error)
                if(error.response.data.detail == 'Given token not valid for any token type') 
                 {
                    this.errorM = "   "
                    this.logout()
                } else if(error.response.data.detail == 'User is inactive'){
                    this.errorM = "    "
                    this.logout()
                } else{
                    this.errorM = ' ,   '
                }
                this.$toast.error(this.errorM, {
                    position: "bottom-left",
                    timeout: 5000,
                    closeOnClick: true,
                    pauseOnFocusLoss: true,
                    pauseOnHover: true,
                    draggable: true,
                    draggablePercent: 0.6,
                    showCloseButtonOnHover: false,
                    hideProgressBar: false,
                    closeButton: "button",
                    icon: true,
                    rtl: false
                });
                this.dialog = false
            })
        },
        close () {
            this.dialog = false
        },
        closeComponent(){
            this.$emit('closeComponent')
        },
        logout() {
            axios.post('http://127.0.0.1:8000/api/logout/', {
                refresh_token: sessionStorage.getItem('refresh')
            })
            .then((response) => {
                console.log(response)
                //localStorage.removeItem('access')
                //localStorage.removeItem('refresh')
                sessionStorage.clear();
                this.loginPage()
            })
            .catch((error)=>{
                console.log(error)
            })
        },
        loginPage() {
            router.push({ path: "/login" }).catch((err) => {
                // Ignore the vuex err regarding  navigating to the page they are already on.
                if (
                err.name !== "NavigationDuplicated" &&
                !err.message.includes(
                    "Avoided redundant navigation to current location"
                )
                ) {
                // But print any other errors to the console
                console.log(err);
                }
            });
        },
        manifestId(){
            let accessToken = JSON.parse(sessionStorage.getItem('access'))
            const baseURL = `http://127.0.0.1:8000/manifest/search/?=`;
            const options = {
                method: 'GET',
                baseURL: baseURL,
                timeout: 5000,
                headers: {
                    Authorization: 'Bearer ' + accessToken.value
                }, 
            };
            axios(options)
            .then((response) => {
                console.log(response)
                this.manifestList = response.data.results
                this.manifestList.unshift({manifest_code: "", id: null})
            })
            .catch((error) => {
                console.log(error)
                if(error.response.data.detail == 'Given token not valid for any token type'){
                    this.$toast.error('    ', {
                    position: "bottom-left",
                    timeout: 5000,
                    closeOnClick: true,
                    pauseOnFocusLoss: true,
                    pauseOnHover: true,
                    draggable: true,
                    draggablePercent: 0.6,
                    showCloseButtonOnHover: false,
                    hideProgressBar: false,
                    closeButton: "button",
                    icon: true,
                    rtl: false
                });
                    this.logout()
                }
                this.$toast.error(error.response.data.detail, {
                    position: "bottom-left",
                    timeout: 5000,
                    closeOnClick: true,
                    pauseOnFocusLoss: true,
                    pauseOnHover: true,
                    draggable: true,
                    draggablePercent: 0.6,
                    showCloseButtonOnHover: false,
                    hideProgressBar: false,
                    closeButton: "button",
                    icon: true,
                    rtl: false
                });
                
            })
        },
    },
    mounted(){
        this.manifestId()
        console.log(this.currency)
    }  
}
</script>

